import { logActivity } from "./logger";

export interface AuditEntry {
    admin_id: string;
    action: string;
    resource_type: string;
    resource_id: string;
    old_value?: any;
    new_value?: any;
}

/**
 * Audit logging for admin actions
 * Tracks data changes with before/after states
 */
export async function logAudit({
    admin_id,
    action,
    resource_type,
    resource_id,
    old_value,
    new_value,
}: AuditEntry) {
    await logActivity({
        user_id: admin_id,
        action: `ADMIN_${action}`, // Prefix for easy filtering
        entity_type: resource_type,
        entity_id: resource_id,
        metadata: {
            audit: true,
            changes: {
                from: old_value,
                to: new_value,
            },
        },
        level: "warning", // Audit logs are important
    });

    // TODO: Insert into specific 'audit_logs' table in D1
}
